import React, { Component } from 'react'
import List from './List-of-playgrounds'
import Header from './Header';


class MainPage extends Component {

	state = {
    playgroundsDetail: [
      {
        "city": "Velk Hotice",
        "address": "Zmeck 197",
        "lat": 49.933055,
        "lng": 17.972581,
        "equipment": []
      },
      {
        "city": "Velk Hotice",
        "address": "Aktov 461",
        "lat": 49.940359,
        "lng": 17.969912,
        "equipment": []
      },
      {
        "city": "Velk Hotice",
        "address": "Mrova 119",
        "lat": 49.927919,
        "lng": 17.978811,
        "equipment": []
      },
      {
        "city": "Kravae",
        "address": "Mrov 3033/27",
        "lat": 49.930684,
        "lng": 17.996235,
        "equipment": []
      },
      {
        "city": "Velk Hotice",
        "address": "Pekliska 48",
        "lat": 49.939094,
        "lng": 17.97441,
        "equipment": []
      }
    ],    
  }
  
  render () {
    
    return (
      <div className="mainPage">
				 <Header/>
			
				<div id="main">
          
					<div id="sideBar">
						<List playgrounds={this.state.playgroundsDetail}/>            
					</div>
		
					
		
				</div>
			</div>          
		)
	}
}

export default MainPage


